@import 'colors';
.title{
  font-size:2.5rem;
  letter-spacing: .5px;
  font-family: "Maven Pro",sans-serif;
  font-weight: 500;
  color:$main

}
.galleryButton{
  transition: all ease-in 300ms;
  background: hsla(43, 89%, 69%, 0.691);;
  text-align: center;
  width: 5rem;
  font-family: 'kraftan',sans-serif;
  color:#ffffff;
  padding: 1rem;
  border-radius: 25rem;
  position: absolute;
  cursor: pointer;
  height: 3rem;
  bottom:25%;
  right:10%;
  @media screen and (max-width:800px) {
    height: 2rem;
    width: 2rem;
    font-size:.75rem;
    right:5%;
    top:42vh;
  }
}
.galleryButton:hover{
  transition: all ease-in 300ms;
  background: $main;
}
.heading{
  @extend .title;
  font-size: 2rem;
}
.Maincontainer{
  display:flex;
  justify-content: space-between;
  margin: 1rem;
  margin-left: 5em;
  max-width: 70%;
  margin-right: 5em;
  @media screen and (max-width:800px) {
    padding:1rem;
    margin: 0 0 0 0;
    display:block;
    position: relative;
    text-align: center;
    align-items: center;
    max-width: 100%;
  }
}


.container::-webkit-scrollbar {
  display:none;
}

.description{
  display: flex;
  text-decoration: none;
  color: black;
  font-family: "Maven Pro",sans-serif;
  font-weight: 400;
  font-size:1.5rem;
  @media screen and (max-width:800px) {
    width:100%;
    font-size:1rem;
    flex-direction: column;
  }
}

.detail{
  font-family: "Maven Pro",sans-serif;
  font-weight: 500;
  color:$main;
  font-size:1.5rem;
  margin-right: 1rem;
  @media screen and (max-width:800px) {
    font-size:1.25rem;
    display:flex;
    margin-left: 1rem;
    margin-right: 0rem;
    flex-direction: column;
  }
}

h2{
  background: $gradient;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.comparator{
  transition: all ease-in 300ms;
  cursor: pointer;
  text-align: center;
  background-color: white;
  box-shadow: 0 0 10px $shadow;
  max-width: 12vw;
  border-radius: 1rem;
  font-family: 'kraftan',sans-serif;
  font-weight: 600;
  color: #313131;
  position: fixed;
  bottom: 10px;
  left: 10px;
  padding: 1rem;
  z-index: 40;
  font-size: .75rem;
  @media screen and (max-width:800px) {
    display: none;
  }
  h2{
    font-size: 1rem;
    margin-top: 0rem;
    margin-bottom: 0rem;
  }
  .site{
    width: 100%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    img{
      width: 10%;
    }
    p{

    }
  }
}